



.list {
    background: linear-gradient(rgba(95, 0, 216, 1),
    rgba(46, 191, 145, 1));
    height: 100vh;
    width: 250px;
    list-style: none;
    vertical-align: middle;
}
.link {
    text-decoration: none;
    color: white;
    line-height: 36px;
    
}


.listItem {
 margin: 20px;
 display: flex; align-items: center
}

.listItem:nth-child(6) {
margin-top: 125%;
  margin-bottom: 5%;
   }


.logo {
    margin-left: 37px;
    margin-top: 22%;
    margin-bottom: 12%;
    display: inline-block;
}

.logoContainer::after {
    opacity: 0.2;
    content: '';
    display: inline-block;
    width: 180px;
    height: 1px;
    background-color: white;
    margin-left: 10%;
    margin-right: 10%;
    margin-bottom: 5%;
}


.listItem_img {
    margin-left: 17px;
    margin-right: 20px;

}